<template>
  <div class="below">
    <div
      class="header"
      :style="{'top': (hideTop?-50:0)+'px','backgroundColor': (transparent?'transparent':'white')}"
    >
      <div class="top">
        <div class="logo">SuperVideo</div>
        <div class="lang" @click="changeLang">
          <span class="text">{{$t("lang")}}</span>
          <i class="icon iconfont iconqiehuan"></i>
        </div>
      </div>
      <div class="bottom">
        <div class="menu">
          <i class="iconfont iconmenu-two"></i>
        </div>
        <div class="input_wrap">
          <i class="search_icon iconfont iconsousuo"></i>
          <input
            :placeholder="$t('searchTips')"
            v-model="search_str"
            @focus="inputFocus"
            @blur="inputBlur"
            ref="input"
          />
          <transition name="myAnim">
            <button class="search_btn" v-show="show_btn" @click="searchClick">{{$t("search")}}</button>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search_str: "", //搜索关键字
      show_btn: false, //显示搜索按钮
      input_focus: false, //输入框获取了焦点
      hideTop: false,//隐藏上半部分
    };
  },
  watch: {
    search_str(val) {
      this.show_btn = this.input_focus || !!val;
    }
  },
  props: {
    transparent: {
      type: Boolean,
      default: false
    } //背景透明
  },
  methods: {
    inputFocus() {
      this.show_btn = this.input_focus = true;
    },
    inputBlur() {
      this.input_focus = false;
      this.show_btn = !!this.search_str;
    },
    searchClick() {
      this.$refs.input.focus();
    },
    changeLang() {
      this.$i18n.locale = this.$i18n.locale == "zh" ? "en" : "zh";
    },
    scrollEvent() {
      this.hideTop = document.documentElement.scrollTop >= 50;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollEvent);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollEvent);
  }
};
</script>

<style lang="less" scoped>
.below {
  width: 100%;
  height: 92px;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100px;
  background-color: white;
  transition: top 0.2s ease-out;
  padding: 0 10px;
  box-sizing: border-box;
}
.top {
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo {
    font-weight: bold;
    font-size: 23px;
  }
  .lang {
    .text {
      margin-right: 2px;
      font-size: 14px;
    }
  }
}
.logo {
  size: 25px;
}
.bottom {
  height: 50%;
  display: flex;
  align-items: center;
}
.menu {
  text-align: center;
  width: 20px;
  height: 20px;
  margin: 0 15px 0 5px;
}
.input_wrap {
  flex: 90%;
  border-radius: 20px;
  background-color: #eee;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px;
  box-sizing: border-box;
  position: relative;
  i {
    margin-top: -4px;
  }
  input {
    outline-style: none;
    background-color: transparent;
    border: none;
    flex: 1;
  }
}
.search_icon {
  width: 15px;
  height: 15px;
  margin-right: 10px;
}
.search_btn {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50px;
  width: 80px;
  height: 100%;
  color: white;
  background-color: orange;
  text-align: center;
  size: 20px;
  border: none;
  outline-style: none;
}

.myAnim-enter-active,
.myAnim-leave-active {
  transition: opacity 0.5s;
}
.myAnim-enter,
.myAnim-leave-to {
  opacity: 0;
}

input::-webkit-input-placeholder {
  /* WebKit browsers 适配谷歌 */
  color: #888;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 适配火狐 */
  color: #888;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ 适配火狐 */
  color: #888;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10+  适配ie*/
  color: #888;
}
</style>